export default {
	data() {
		return {
			phone: '',
			hasPsd:false
		}
	},
	methods: {
		navTo(url) {
			var that=this;
			console.log(url);
			uni.showModal({
				title: '修改登陆密码',
				content: '将给手机' + this.phone + '发送验证码',
				confirmColor:'#C34944',
				success: function(res) {
					if (res.confirm) {
						that.getCode(url);
					}
				}
			})
		},
		getCode(u){
			this.$apiJs.getCode({
				phone:this.$store.state.userInfo.phone,
			}).then(res => {
				if(res){
					this.$commonJs.navTo(u)
				}
			})
		},
		// 判断是否存在密码
		hasPsdFlag(){
			if(this.$store.state.userInfo.pwd_type){
				this.hasPsd=true;
			}
		}

	},
	onLoad() {
		var p = this.$store.state.userInfo.phone;
		this.phone = p.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2');
		this.hasPsdFlag();
		
	}
}
